<template>
    <el-button @click="connect" :loading="loading">123123</el-button>
</template>

<script>
import preSaleABI from '@/api/abis/preSaleABI.json'
export default {
    data() {
        return {
            loading: false,
        }
    },
    async mounted() {
        const { State } = this.$wallet.preSaleABIOnlyRead
        console.log(State)
        const data = await State();
        console.log(data)
    },
    methods: {
        async connect() {
            this.loading = true
            try {
                const res = await this.$wallet.initNetwork()     
                const userInfo = await res.preSaleABI.userInfo(res.accounts[0])
                
                console.log(userInfo) 
                this.loading = false
            } catch (error) {
                this.loading = false
            }
        }
    }
}
</script>

